/* Slider */
.slick-slider
{
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    -webkit-transform: translate3d(0, 0, 0);
       -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
         -o-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
    margin-left: auto;
    margin-right: auto;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}


.Slider-track{
    transition: all 4s linear 0s;
    width: 5697px;
    transform: translate3d(2646px, 0px, 0px);
  }

  .Slider-option{
    display: block;
  }
  
  .Slider-List{
    transition: all 4s linear 0s;
    width: 4890px;
    transform: translate3d(-2147px, 0px, 0px);
  }

  #popular_dish .Slider-dishes .slick-slide:nth-child(odd) .item {
    rotate: -3deg;
    margin: 10px;
  }
  #popular_dish .Slider-dishes .slick-slide {
    margin: 15px;
    width: 243.75px !important;
    position: relative;
  }
  #popular_dish .Slider-dishes .slick-slide:nth-child(even) .item {
    rotate: 3deg;
    margin: 10px;
  }
  #popular_dish .Slider-dishes .slick-slide:nth-child(4n + 2) .item {
    transform: translateY(-30px);
  }